import * as React from "react"
import SEO from "../../components/seo";
import { CodeSample, SectionWrapper } from "../../components/ExampleComponents";

const zIndex = [ '1', '2', '3', '4', '5', '6', '7', '8', '9' ]
const zIndexCodeString = zIndex.map( z => `<div class="z--${ z }">...</div>\n` )

const ZIndex = () => {
    return (
        <>
            <SEO title={ "Z Index" }/>
            <h1>Z Index</h1>
            <p>The class constructor looks like this:</p>
            <CodeSample label={ `For z index` } codeString={ `z--{value}` }/>
            <SectionWrapper id={ 'z-index' } codeString={ zIndexCodeString.join( '' ) }>
                <p>You can use z-index to layer things on your page.</p>
            </SectionWrapper>
        </>
    )
}

export default ZIndex
